import * as React from "react";

import Icons from "assets/icons";

import { useDispatchAction } from "hooks";

const ModalCloseButton = () => {
    const { hideModal } = useDispatchAction();
    const handleClick = React.useCallback(() => hideModal(), [hideModal]);
    return (
        <div className="modal-close__wrapper">
            <button aria-label="Close" className="modal-close__button" type="button" onClick={handleClick}>
                <Icons.Close />
            </button>
        </div>
    );
};

export default ModalCloseButton;
